@import url('https://fonts.googleapis.com/css2?family=Asset&family=Figtree:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


:root{
    --green-1: #B2E7BC;
    --green-2: #92DDA0;
    --green-3: #73D385;
    --green-4: #56A264;
    --money: #598C58;
    --purple: #6048a3;
    --red: #9e4848;
    --off-black: #000c03;
    --off-light: #E8F0E9;
}



.custom-wrapper{
    width: 100vw;
    height: 100vh;
    position: fixed;
    background-color: #030303;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.header-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 95%;
}

.app-body{
    width: 98%;
    height: 85%;
    background-color: #111111;
    border-radius: 0.5rem;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
}

.chart-container{
    display: flex;
    justify-content: center;
    width: 100%;
}

.table-container{
    display: flex;
    justify-content: center;
    width: 100%;
}

.app-title{
    font-size: 3rem;
    color: #00000000;
    font-family: 'Asset', cursive;
    -webkit-text-stroke: 1px var(--money);
    font-weight: 700;
    font-style: italic;
}

#unstyled{
    text-decoration: none;
}

.home-screen{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#drawer-pull{
    color: var(--off-light)
}

.bill-logo{
    width:40%;
}

#pie-box{
    width: 30%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin: 2rem 4rem;
}

#pie-box h3{
    font-family: 'Figtree', sans-serif;
    color: var(--off-light);
    font-size: 2.5rem;
}

#custom-input{
    display: flex;
    text-decoration: none;
}

#home-button{
    background-color: #598c5880;
}

#home-button:hover{
    background-color: #598c58;
}

#user-button{
    background-color: #598c5880;
}

#user-button:hover{
    background-color: #598c58;
}

h6{
    color: var(--green-1);
    font-family: 'Figtree', sans-serif;
    font-weight: 300;
    font-size: 1.5rem;
}


/* Responsive-layouts */

@media screen and (max-width: 720px) {
    .app-body{flex-direction: column; justify-content: flex-start; align-items: center;}
    .chart-container{flex-direction: column; align-items: center; margin-top: 1rem;}
    #pie-box{width: 60%; margin: 1rem;}
    #pie-box h3{font-size: 2rem;}
  }